.navHeader {
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 1px solid lightgrey;
    margin-bottom: 10px;
}

.halfWay {
    width: 50%;
    align-items: center;
}

.leftHalf {
    justify-content: flex-start;
}

.rightHalf {
    justify-content: flex-end;
}

.navHomeIcon {
    margin: 5px;
    height: 70px;
    width: 93px;
}

.signoutIcon {
    margin: 5px;
    height: 45px;
    width: 45px;
}

.floatLeft {
    float: left;
}

.floatRight {
    float: right;
}

.navButton {
    min-width: 150px;
    margin-left: 5%;
    transform: translateY(50%);
}

.signOut {
    transform: translateY(13%);
    width: 137px;
    float: right;
    margin-right: 20px;
    cursor: pointer;
    padding: 3px;
    padding-right: 7px;
    border-radius: 5px;
    border: 1px solid lightgray;
    font-weight: 600;
    background-color: rgb(247, 247, 247);
}

.notifications {
    margin-right: 5%;
}

.verifyEmailDiv {
    text-align: center;
    font-weight: 600;
}

.sentEmail {
    display: flex;
    justify-content: center;
    align-items: center;
}